html {
  scroll-behavior: smooth;
}
body {
  min-width: 320px;
}
.fsz-18 {
  font-size: 18px;
}
.d-none {
  display: none;
}
.text-primary {
  color: $primary;
}
.text-center {
  text-align: center;
}
.lp-wrp {
  font-size: 15px;
  color: #000000;
  svg {
    width: 1em;
    height: 1em;
  }
  img, svg {
    vertical-align: middle;
  }
  img {
    max-width: 100%;
    height: auto;
  }
  a {
    color: $link-color;
    &:hover {
      color: $primary;
      text-decoration: underline;
    }
  }
  .top-heading {
    text-align: center;
    h3 {
      font-weight: 300;
      font-size: 20px;
      strong {
        font-weight: 900;
      }
    }
  }
  .h2 {
    color: $primary;
    font-weight: 700;
    font-size: 22px;
    line-height: 1.33;
  }
  .h3 {
    color: $primary;
    font-weight: 500;
    font-size: 24px;
    line-height: 1.33;
  }
  .pt-4 {
    padding-top: 2.5rem;
  }
  .pb-4 {
    padding-bottom: 2.5rem;
  }
  .py-4 {
    padding: 2.5rem 0;
  }
  .pt-5 {
    padding-top: 3rem;
  }
  .pb-5 {
    padding-bottom: 3rem;
  }
  .py-5 {
    padding: 3rem 0;
  }
  .mb-1, .mb-2 {
    margin-bottom: 1rem;
  }
}
.lp-banner {
  background-color: $primary;
  padding: 2rem 0;
  .hbspt-form {
    .input {
      margin-bottom: 5px;
      input, textarea, select {
        font-size: 14px;
        width: 100%;
        border-radius: 19px;
        padding: 8px 20px;
      }
      select {
        border-color: #fff;
      }
      textarea {
        min-height: 110px;
      }
    }
    .hs-form-booleancheckbox-display {
      font-size: 13px;
      line-height: 1.1;
      display: grid;
      grid-template-columns: 1em auto;
      gap: 0.5em;
      input[type="checkbox"] {
        -webkit-appearance: none;
        appearance: none;
        background-color: $primary;
        margin: 0;
        font: inherit;
        color: currentColor;
        width: 1.5em;
        height: 1.5em;
        padding: 0;
        border-radius: 50%;
        //  transform: translateY(-0.075em);
        display: grid;
        place-content: center;
      }
      input[type="checkbox"]::before {
        content: "";
        width: 0.65em;
        height: 0.65em;
        clip-path: polygon(14% 44%, 0 65%, 50% 100%, 100% 16%, 80% 0%, 43% 62%);
        transform: scale(0);
        transform-origin: bottom left;
        transition: 120ms transform ease-in-out;
        box-shadow: inset 1em 1em #fff;
        background-color: CanvasText;
      }
      input[type="checkbox"]:checked::before {
        transform: scale(1);
      }
    }
    label {
      font-size: 13px;
      margin-bottom: 5px;
      margin-left: 10px;
    }
    .hs-error-msgs {
      margin-top: -5px;
    }
    .hs-error-msg {
      font-size: 11px;
      color: red;
    }
  }
}
.what-is-sm {
  position: relative;
  overflow: hidden;
  &:after {
    position: absolute;
    z-index: -1;
    content: '';
    display: block;
    background-color: #eeeeee;
    left: 30%;
    right: 0;
    top: 0;
    bottom: 0;
    border-radius: 19px;
  }
  img {
    border-radius: 19px;
  }
}
.sm-benefit {
  flex: 0 0 auto;
  width: 50%;
  font-size: 15px;
  text-transform: uppercase;
  color: #575756;
  font-weight: 500;
  padding-top: 1rem;
  padding-bottom: 1rem;
  border-radius: 19px;
  transition: all .5s ease;
  p {
    transition: all .3s ease;
  }
  .icon {
    transition: all .3s ease;
    font-size: 22px;
    color: #8870a4;
    margin-bottom: 1rem;
  }
  &:hover {
    cursor: pointer;
    background-color: #eeeeee;
    padding: 1rem;
    font-weight: 700;
    .icon {
      color: #feb70d;
    }
  }
}
.why-choose-cs {
  .row {
    justify-content: space-between;
  }
  .icon-wrp {
    font-size: 26px;
    width: 3.05em;
    height: 3.05em;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 4px solid #dddddd;
    margin-top: 1rem;
  }
  .icon {
    color: $primary;
  }
}