.container {
  --lp-gutter-x: 1.5rem;
  --lp-gutter-y: 0;
  width: 100%;
  padding-right: calc(var(--lp-gutter-x) * 0.5);
  padding-left: calc(var(--lp-gutter-x) * 0.5);
  margin-right: auto;
  margin-left: auto;
}
@media (min-width: 576px) {
  .container {
    max-width: 540px;
  }
}
@media (min-width: 768px) {
  .container {
    max-width: 720px;
  }
}
@media (min-width: 992px) {
  .container {
    max-width: 960px;
  }
}
@media (min-width: 1200px) {
  .container {
    max-width: 1154px;
  }
}
.row {
  --lp-gutter-x: 1.5rem;
  --lp-gutter-y: 0;
  display: flex;
  flex-wrap: wrap;
  margin-top: calc(-1 * var(--lp-gutter-y));
  margin-right: calc(-0.5 * var(--lp-gutter-x));
  margin-left: calc(-0.5 * var(--lp-gutter-x));
}
.row > * {
  box-sizing: border-box;
  flex-shrink: 0;
  width: 100%;
  max-width: 100%;
  padding-right: calc(var(--lp-gutter-x) * 0.5);
  padding-left: calc(var(--lp-gutter-x) * 0.5);
  margin-top: var(--lp-gutter-y);
}
.col {
  flex: 1 0 0%;
}
.align-items-center {
  align-items: center;
}
